*{
  margin: 0;
  padding: 0;
  color: #ffffff;
}
.App{
  background-color: #1b162b;
  padding: 70px;
  margin-bottom: -50px;
}
.row{
  display: flex;
  flex: 100%;
  margin-top: 50px;
}
.column-1{
  flex: 40%;
}
.column-2{
  flex: 60%;
  margin: 30px;
}
.name img{
  margin-right: 20px;
}
.quote{
  text-align: center;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 40px;
}
.name{
  font-size: 48px;
  font-family: 'Arial';
  font-weight: 600;
}
.column1{
  flex: 75%;
}
.column2{
  flex: 25%;
  padding-top: 150px;
}
ul{
  list-style-type: none;
}
.icon-list{
  margin-left: 15px;
}
ul li{
  margin-bottom: 20px;
}
ul li span{
  font-size: 24px;
}
.callme{
  font-size: 40px;
  font-weight: 700;
  text-decoration: none;
}
.column2 a{
  text-decoration: none;
}
.home{
  padding: 10px;
  padding-right: 30px;
  padding-left: 30px;
  background-color: #221f1f;
  border-radius: 10px;
  text-decoration: none;
}
.home:hover{
  background-color: #ffffff;
  color: #221f1f;
  transition: 0.7s;
}